.more {
  fill: #131313;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-top: auto;
  transition: fill .3s cubic-bezier(.4, 0, .2, 1), scale .3s cubic-bezier(.4, 0, .2, 1);
}

.more:hover, .more:focus {
  fill: #fcfcfc;
  animation: move 1.5s infinite;
}

@keyframes move {
  0% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(15px);
  }

  100% {
    transform: translateX(0);
  }
}

